import React, { Component, Fragment } from "react"
import { withRouter, Link } from "react-router-dom"

import { AppContext } from "../../utils/helpers"
import { getFilenamesInDir } from "../../utils/crud"

import SocialList from "./../SocialList"
import "./Header.css"

const ABOUT_BACKGROUND = "/images/header_background/flowers.jpg"
const IMAGES_DIR = "images"

class Header extends Component {
  state = {
    headerStyle: null,
    filenames: null,
  }

  static getRandomFilenameURL(filenames) {
    if (!filenames) {
      return ""
    }
    const randIndex = Math.floor(Math.random() * filenames.length)
    return `${process.env.REACT_APP_API_URL}static/${IMAGES_DIR}/${filenames[randIndex]}`
  }

  static getDerivedStateFromProps(props, state) {
    if (props.location.pathname === "/about") {
      return {
        headerStyle: {
          backgroundImage: `url(${ABOUT_BACKGROUND})`,
          minHeight: "450px",
        },
      }
    }

    if (
      props.location.pathname !== "/" &&
      props.location.pathname !== "/1"
    ) {
      return {
        headerStyle: {
          backgroundImage: "none",
          minHeight: "0",
        },
      }
    }

    if (state.filenames) {
      return {
        headerStyle: {
          backgroundImage: `url(${Header.getRandomFilenameURL(
            state.filenames,
          )})`,
          minHeight: "250px",
        },
      }
    }
    return null
  }

  async componentDidMount() {
    const resp = await getFilenamesInDir(`${IMAGES_DIR}/*`)
    if (resp.error !== "") {
      console.error(resp.error)
      return null
    }
    const filenames = resp.body.filenames
    this.setState({
      filenames,
      headerStyle: {
        backgroundImage: `url(${Header.getRandomFilenameURL(
          filenames,
        )})`,
        minHeight: "250px",
      },
    })
  }

  render() {
    return (
      <header style={this.state.headerStyle}>
        <nav className="nav-wrapper transparent">
          <div className="container">
            <Link to="/1" className="brand-logo">
              <span className="flow-text">
                {process.env.REACT_APP_SITE_TITLE}
              </span>
            </Link>
            <a
              href="#menu"
              className="sidenav-trigger"
              data-target="mobile-links"
            >
              <i className="material-icons">menu</i>
            </a>
            <ul id="link-list" className="hide-on-med-and-down right">
              <Links />
            </ul>
          </div>
        </nav>

        <ul id="mobile-links" className="sidenav grey darken-4">
          <ul className="grey darken-3">
            <li>
              <a className="subheader grey-text text-darken-1">
                Menu
              </a>
            </li>
            <Links />
            <li>
              <div className="divider grey"></div>
            </li>
          </ul>

          <li>
            <a className="subheader grey-text text-darken-1">
              Me on socials
            </a>
          </li>
          <SocialList />
        </ul>
      </header>
    )
  }
}

const Links = () => (
  <Fragment>
    <li>
      <Link to="/1" className="sidenav-close waves-effect">
        <span>Feed</span>
      </Link>
    </li>

    <li>
      <Link to="/about" className="sidenav-close waves-effect">
        About me
      </Link>
    </li>
    <li>
      <AppContext.Consumer>
        {({ isAuthorized }) =>
          isAuthorized ? (
            <Link to="/logout" className="sidenav-close waves-effect">
              Logout
            </Link>
          ) : (
            <Link to="/login" className="sidenav-close waves-effect">
              Login
            </Link>
          )
        }
      </AppContext.Consumer>
    </li>
  </Fragment>
)

export default withRouter(Header)
